$apricot: #fca311;
$platinum: #e5e5e5;
$white: #ffffff;

.header {
  position: relative;
  min-height: 100px;
  background-color: $platinum;
  padding: 10px 0;
  width: 100%;
  min-width: 650px;
}

.blog {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 35px;
  font-weight: 700;
}
.LinkWraper {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.navLink {
  color: black;
  text-decoration: none;
  letter-spacing: 2px;
  text-transform: uppercase;
  transition: all 0.2s;
  padding: 5px;
  &:hover {
    opacity: 0.8;
  }
}
.user {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 22px;
  font-style: italic;
}
.paragraph {
  padding: 0;
  font-size: 16px;
  margin: 0;
}
.active {
  text-decoration: underline;
}
